import { useEffect, useState } from 'react';
import './App.scss';
const App = () => {

  const [results, setResults] = useState([]);
  const [stats, setStats] = useState({});

  const renderChanges = (mutant, changes) => {
    const newChanges = changes?.map(result => { return <a href={`${process.env.PUBLIC_URL}/Resultados/${mutant}/Screenshots/Change/${result.file}`} className="m-2">{result.file.replace(".png", "")}</a> }) || [];
    return newChanges;
  };

  useEffect(() => {
    const newStats = { errors: 0, vrt: 0, e2e: 0, trivials: 0 };
    results.forEach(result => {
      newStats.errors += result.errors;
      newStats.vrt += result.changes ? result.changes.length : 0;
      // newStats.e2e += result.errorTests || 0;
      if (result.signRenaming || result.installing || result.signing) {
        newStats.trivials++;
      }
    });
    setStats(newStats);
  }, [results]);

  useEffect(() => {
    fetch("results.json").then(res => res.json()).then(json => setResults(json));
  }, []);

  return (
    <div className="App">
      <h3 class="mb-2">Parcial 2 - Pruebas Automáticas</h3>
      <div id="team">
        <h4>Integrantes</h4>
        <ul>
          <li>Andrés Varón</li>
          <li>Gabriel Pinto</li>
          <li>Jeisson Ávila</li>
          <li>Luis Martinez</li>
        </ul>
      </div>
      <hr class="bg-white mt-4 mb-4"></hr>
      <div className="row">
        <div className="col-12">
          <h3 className="mb-2">Estadísticas</h3>
        </div>
        <div className="col-md-6">
          <h1>{results.length}</h1>
          <h5>Mutantes</h5>
        </div>
        <div className="col-md-6">
          <h1>{stats.errors}</h1>
          <h5>Errores totales</h5>
        </div>
        <div className="col-12 mt-4"></div>
        <div className="col-md-4">
          <h1>{stats.vrt}</h1>
          <h5>Errores VRT</h5>
        </div>
        <div className="col-md-4">
          <h1>{stats.e2e}</h1>
          <h5>Errores E2E</h5>
        </div>
        <div className="col-md-4">
          <h1>{stats.trivials}</h1>
          <h5>Errores triviales</h5>
        </div>
      </div>
      <hr class="bg-white mt-4 mb-4"></hr>
      <h3 className="mb-4">Resultados</h3>
      <div class="table-responsive rounded">
        <table class="table text-white table-dark table-hover rounded shadow">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Errores</th>
              <th scope="col">VRT</th>
              <th scope="col">E2E</th>
              <th scope="col">Trivial</th>
              <th scope="col">Evidencia</th>
            </tr>
          </thead>
          <tbody>
            {results.map(result => <tr>
              <th scope="row">{result.mutant}</th>
              <td>{result.errors}</td>
              <td>{result.changes?.length || 0}{result.changes?.length > 0 && <p className="mb-0">{renderChanges(result.mutant, result.changes)}</p>}</td>
              <td>{result.errorTests || 0}</td>
              <td>{(result.signRenaming || result.installing || result.signing) && <>&#10004;</>}</td>
              <td><a href={`${process.env.PUBLIC_URL}/Resultados/${result.mutant}/ERROR.txt`}>Archivo</a></td>
            </tr>)}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default App;
